$color: #29b6f6;
//$color: #6cb88b;
$pColor: #333;

//#42b983

* {box-sizing: border-box;}


body {background: #eee;margin: 20px 0;}

ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: $pColor;
}

hr {background-color: $color;height: 1px;border: none;}

p {color: $pColor;margin: .3em 0;}

h1 {font-size: 25px;margin: 0 0 10px 0;}

h2 {color: $color;margin: 0 0 10px 0;font-weight: normal;}

h3 {color: $pColor;margin: 0;}

h6 {margin: 0;font-weight: normal;}

a {
  color: $pColor;
  text-decoration: none;
  border-bottom: 1px solid;
}

strong {font-weight: normal;color: $color;}


article {
  width: 21cm;
  background: white;
  margin: 0 auto;
  overflow: auto;
  padding: 1em;
  > .bio {
    text-align: center;
    position: relative;
    > p {
      font-size: 14px;
    }
    > img {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  .work-experience {
    > ul {
      margin-top: 0;
      list-style-position: inside;
      padding: 0;
      .view {display: none;}
      > li {
        position: relative;
        margin-bottom: 15px;
        > header {display: flex;justify-content: space-between;}
        > ul {
          list-style-type: disc;
          padding-left: 20px;
          margin-top: 10px;
          > li {
            margin-bottom: 3px;
          }
        }
      }
    }
  }
  .projects {
    > ol {
      margin-top: 0;
      list-style-position: inside;
      padding: 0;
      .view {display: none;}
      > li {
        position: relative;
        margin-bottom: 15px;
        > header {display: flex;justify-content: space-between;}
        > .overview {margin: 8px 0;font-weight: bold;}
        > .qr-code {
          position: absolute;
          bottom: 0;
          right: 0;
          width: 80px;
          height: 80px;
        }
        > ul {
          list-style-type: disc;
          padding-left: 20px;
          margin-top: 10px;
          > li {
            margin-bottom: 3px;
          }
        }
      }
    }
  }

  .skills > .wrapper {
    display: flex;
    justify-content: space-between;
    > ul {
      list-style-type: disc;
      padding: 15px 0;
      margin-left: 20px;
      > li {line-height: 1.2;margin-bottom: 8px;}
    }
    > .x {
      width: 330px;
      height: 270px;
      margin: auto 0;
      display: none;
    }
  }
  .openSource {
    > ul > li {
      margin-bottom: 10px;
      &.contain-img {
        margin-bottom: 0;
      }
      > header {
        position: relative;
        display: flex;
        justify-content: space-between;
      }
      > .p-img {
        display: flex;
        justify-content: space-between;
        &.clock {
          > img {
            height: 88px;
          }
        }
        > img {
          width: 80px;
          height: 80px;
          border-radius: 3px;
        }
      }
    }
  }
  .others {
    > ul {
      > li {
        > a {
          color: $color;
          font-weight: bold;
          font-size: 20px;
        }
      }
    }

  }
  .education {
    > h2 {margin-top: 15px;}
    > ul > li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      > p {
        display: block;
        width: 200px;
      }
    }
  }

}


@media (max-width: 500px) {
  body {margin: 0;}
  .view {display: none;}
  article {width: auto;height: auto;}
  .bio img {display: none;}
  .skills .wrapper {flex-direction: column;justify-content: center;}
  #skills {width: auto;}
  .education {
    > ul > li {
      flex-direction: column;
      text-align: center;
    }
  }
}








